import { defineComponent, watch, ref, onMounted } from "vue"; // 对播放量进行格式化

import { formatNumber } from "@/utils/formatNumber";
import { useRouter } from "vue-router";
import { useStore } from "vuex";
import { playSongMenuList } from "@/hook/playSongMenuList";
export default defineComponent({
  name: "",
  components: {},
  props: {
    songMenuItem: {
      type: Array,
      default: []
    }
  },

  setup(props) {
    const router = useRouter();
    const isShow = ref(true);
    const store = useStore();
    const size = {
      xs: {
        span: 8
      },
      sm: {
        span: 6
      },
      md: {
        span: 6
      },
      lg: {
        span: 4
      },
      xl: {
        span: 4
      }
    };
    watch(() => props.songMenuItem, value => {
      if (value.length !== 0) {
        console.log("object");
        isShow.value = false;
      }
    }, {
      immediate: true
    }); // 跳转至歌单详情页

    const songlist = id => {
      router.push({
        path: "songMenuDetailPage",
        query: {
          id: id
        }
      });
    }; // 鼠标移入的对象


    const imgid = ref(0); // 鼠标移入

    const onmouseover = item => {
      imgid.value = item.id;
    }; // 鼠标移出


    const onmouseleave = item => {
      imgid.value = 0;
    }; // play图标点击事件


    const playMusic = (event, id) => {
      // 阻止父元素的点击事件
      event.stopPropagation(); // 传入歌单id播放歌曲

      playSongMenuList(id);
    };

    return {
      isShow,
      imgid,
      songlist,
      formatNumber,
      onmouseover,
      onmouseleave,
      playMusic,
      size
    };
  }

});